.burger:after {
  content: "";
  width: 24px;
  height: 2px;
  background-color: #d41443;
  border-radius: 4px;
  transition: transform .35s cubic-bezier(.175, .885, .32, 1.275), background-color .35s cubic-bezier(.175, .885, .32, 1.275);
  position: absolute;
  top: 12px;
  left: 8px;
}

.burger:before {
  content: "";
  width: 24px;
  height: 2px;
  background-color: #d41443;
  border-radius: 4px;
  transition: transform .35s cubic-bezier(.175, .885, .32, 1.275), background-color .35s cubic-bezier(.175, .885, .32, 1.275);
